(() => {
  const refs = {
    // openModalBtn: document.querySelector("[modal-open-price]"),

    openModalBtn: document.querySelectorAll("[modal-open-room]").forEach(btn => { 
      btn.addEventListener('click', toggleModal) 
     }),

    closeModalBtn: document.querySelector("[modal-close-room]"),
    modal: document.querySelector("[modal-room]"),
  };
  
  // refs.openModalBtn.addEventListener("click", toggleModal);
  refs.closeModalBtn.addEventListener("click", toggleModal);

  function toggleModal() {
    refs.modal.classList.toggle("is-hidden");
  }
})();